@import "vars";
@import "libs";
@import "fonts";
@import "global";


.header {
  position: relative;
  height: 100vh;
}

.header__slider {
  &-item {
    height: 100vh;
    background-image: url('../img/header-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
  }
}

.slider-item__info {
  position: absolute;
  right: 80px;
  bottom: 116px;
  text-align: right;
  text-transform: uppercase;
}

.slider-item__info-suptitle {
  margin-bottom: 5px;
}

.slider-item__info-suptext {
  margin-bottom: 8px;
}

.slider-item__info-title {
  font-weight: 800;
  font-size: 80px;
  line-height: 86px;
  margin-bottom: 48px;
}

.slider-item__info-text {
  font-weight: 800;
  font-size: 40px;
  line-height: 46px;
  margin-bottom: 20px;
}

.slider-item__info-link img {
  display: inline-block;
}

.slider-arrows {
  position: absolute;
  top: 40px;
  right: 80px;
  cursor: pointer;
  z-index: 4;
}

.slider-arrows__left {
  right: 125px;
}

.slider-dots {
  position: absolute;
  right: 50px;
  bottom: 36px;
  max-width: 920px;
  width: 100%;
  color: #fff;

  .slick-current {
    .dots-box {
      border-top: 4px solid $green;
    }
  }
}

.dots-box {
  width: 200px;
  display: flex;
  justify-content: space-between;
  height: 28px;
  align-items: center;
  border-top: 4px solid #fff;
  padding-top: 6px;

  &__number {
    font-weight: 800;
    font-size: 24px;
    line-height: 34px;

    text-transform: uppercase;
  }

  &__name {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
  }
}

.header__aside {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  border-right: 1px solid rgba(255, 255, 255, .3);
  height: 100vh;
  text-align: center;
}

.header__title {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4;
  max-width: 467px;
  font-weight: 800;
  font-size: 200px;
  line-height: 156px;

  text-transform: uppercase;

  opacity: 0.07;
}

.menu {
  padding-top: 40px;
  a {
    span {
      display: block;
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 3px;
      padding-top: 6px;
      margin-bottom: 20px;
    }
  }
}

.header__search{
  padding-top: 15px;
  padding-bottom: 35px;
}

.header__date{
  font-weight: 800;
  font-size: 72px;
  line-height: 64px;
  }
  span{
    display: block;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;

    letter-spacing: 1.4px;
    margin-bottom: 30px;
    padding-top: 15px;
}

.header__location{
  display: inline-block;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;

  letter-spacing: 1.05px;
  position: relative;
  padding-left: 19px;
  &::before{

    content: '';
    background-image: url('../img/marker.svg');
    position: absolute;
    width: 11px;
    height: 13px;
    top: 0;
    left: 0;
  }
}


@import "media";
